export default {
  props: ["type", "openid"],
  mounted: function mounted() {
    var _this = this;
    this.$nextTick(function () {
      window.top.WWOpenData.bind(_this.$el);
      window.top.WWOpenData.on("error", function (err) {
        return _this.$emit("openDataError", err);
      });
    });
  }
};